.main-title {
	position: relative;
	max-width: 540px;
	margin-bottom: 65px;
	padding-left: 3px;
	@include sfuitext;
	font-size: 42px;
	line-height: 1.2;
	font-weight: 800;
	text-transform: uppercase;
	color: #f7f7f7;
	letter-spacing: .5px;

	&:after {
		position: absolute;
		bottom: -37px;
		left: 0;
		content: "";
		width: 72px;
		height: 4px;
		opacity: .4;
		background-color: #f7f7f7;

		@include xs {
			bottom: -18px; } }

	@include xs {
		margin-bottom: 45px;
		padding-left: 0px;
		font-size: 30px; } }
