.language {
	$this: &;

	&__list {
		display: flex; }

	&__item {
		position: relative;

		&.is-active {
			&::before {
				content: "";
				z-index: -1;
				position: absolute;
				top: 49%;
				left: 50%;
				transform: translate(-50%, -50%);
				width: 41px;
				height: 29px;
				background-image: url(../img/icon/diamond.svg);
				background-size: contain;
				background-repeat: no-repeat;
				background-position: center; }

			&:hover {
				color: inherit; }

			#{$this}__link {
				color: #f1f5f8; } } }

	&__link {
		display: block;
		padding: 9px;
		color: #353744;
		font-size: 12px;
		line-height: 1.25;
		font-weight: 700;
		text-transform: uppercase;
		text-decoration: none;

		&:hover {
			color: #63b250; } } }
