.hero {

	&__wrap {
		display: flex;

		@include sm {
			flex-direction: column; } }

	&__info {
		position: relative;
		width: calc(50% - 36px);
		padding: 50px 0px;
		color: #fff;
		background-color: #353744;

		@include sm {
			width: 100%; }

		&::before {
			content: "";
			position: absolute;
			bottom: 0;
			right: -36px;
			width: 100%;
			height: 100%;
			z-index: 1;
			background-color: transparent;
			background-image: url(../img/hero/hero-bg-icon.png);
			background-repeat: no-repeat;
			background-position: calc(100% + 90px) 96%;

			@include sm {
				display: none; } } }

	&__info-wrap {
		z-index: 1;
		position: relative;
		top: 48.5%;
		transform: translate(0, -50%);
		right: 0;
		width: calc(1234px / 2 - 30px);
		padding-left: 18px;
		margin-left: auto;

		@include sm {
			top: auto;
			transform: none;
			right: auto;
			width: auto;
			padding-left: 20px;
			margin-left: auto;
			padding: 50px 10px 98px 42px; }

		@include xs {
			padding: 20px 15px 15px 20px; } }

	&__slider {
		position: relative;
		width: calc(50% + 36px);
		overflow: hidden;

		@include sm {
			width: 100%; } }

	&__btn {
		position: absolute;
		left: 0;
		top: 50%;
		transform: translate(0, -50%);
		width: 36px;
		height: 74px;
		background-image: url(../img/hero/hero-btn.png);
		background-repeat: no-repeat;

		@include sm {
			display: none; }

		&::before {
			content: "";
			position: absolute;
			bottom: 99%;
			left: 0;
			width: 36px;
			height: 100%;
			background-color: #353744;
			height: 100vw; }

		&::after {
			content: "";
			position: absolute;
			top: 99%;
			left: 0;
			width: 36px;
			height: 100%;
			background-color: #353744;
			height: 100vw; } }

	&__descr {
		color: #f7f7f7;
		font-size: 20px;
		line-height: 1.2;
		font-weight: 400;

		@include xs {
			font-size: 16px; } }

	&__breadcrumbs {
		margin-bottom: 14px;
		padding-left: 3px; }

	&__dots {
		position: absolute;
		bottom: 17px;
		left: calc(50% + 18px);
		transform: translate(-50%, 0);

		@include sm {
			left: 50%; }

		@include xs {
			width: 100%;
			text-align: center; } } }
