.btn-tab {
	position: relative;
	z-index: 1;
	display: inline-block;
	padding: 7px 26px;
	background-color: #63b250;
	background: transparent;
	@include ptastrasans;
	line-height: 1.15;
	font-weight: 400;
	font-size: 25px;
	color: #fff;
	user-select: none;
	touch-action: manipulation;

	&:not(:last-child) {
		margin-right: 10px; }

	&::before {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: -1;
		background: #63b250;
		transform: skew(-23deg); }

	&:hover,
	&:focus {
		&::before {
			box-shadow: -5px -4px 0 rgba(0, 0, 0, 0.26);
			transform: skew(-23deg) translate(3px,3px);
			transition: box-shadow 200ms linear 0ms, transform 200ms linear 0ms, text-shadow 200ms linear 0ms; } }

	&.is-active::before {
		background-color: #353744; }

	@include xs {
		padding: 7px 22px;
		font-size: 18px; } }
