.about-sect {
	padding: 52px 0 76px;
	background-color: #f7f7f7;

	@include xs {
		padding: 45px 0; }

	&__text {
		p {
			padding-right: 5px;
			color: #313238;
			font-size: 20px;
			line-height: 1.2;
			font-weight: 400;
			opacity: 0.5;

			@include xs {
				padding-left: 5px;
				font-size: 16px;
				opacity: .85; } }

		a {
			color: #313238; } } }
