.breadcrumbs {
	&__list {
		display: flex;
		align-items: center; }

	&__item {
		padding-right: 22px; }

	&__link {
		display: block;
		position: relative;
		font-weight: 400;
		color: #f7f7f7;
		opacity: 0.4;
		font-size: 16px;
		text-decoration: none;
		transition: opacity .25s ease;

		&:focus,
		&:hover {
			opacity: 1; }

		&::after {
			content: "";
			position: absolute;
			top: 8px;
			right: -15px;
			width: 10px;
			height: 10px;
			background-image: url(../img/icon/arrow.png);
			background-repeat: no-repeat;
			background-size: contain; } } }
