.footer {
	padding-top: 41px;
	padding-bottom: 34px;

	&__wrap {
		display: flex;
		justify-content: space-between;
		align-items: center;

		@include xs {
			flex-direction: column;
			justify-content: center; } }

	&__copiright {
		color: #313238;
		font-size: 20px;
		font-weight: 400;

		@include xs {
			margin-bottom: 20px;
			font-size: 18px; } }

	&__logo {
		display: block; }

	&__logo-img {
		width: 150px;
		height: 30px;
		object-fit: cover; } }
