@include font-weight('ptastrasans', ptastrasans, 400);
@include font-weight('ptastrasans', ptastrasansbold, 700);
@include font-weight('sfuitext', sfuitext-heavy, 800);

@mixin ptastrasans {
	font-family: "ptastrasans", sans-serif; }

@mixin sfuitext {
	font-family: "sfuitext", sans-serif; }

body {
	@include ptastrasans;
	background: #FFF;
	font-size: 16px;
	line-height: 1.4;
	font-weight: 400;

	&.is-overflow-hidden {
		height: 100vh;
		overflow: hidden;
		width: 100vw; } }

a {
	color: inherit;
	font-size: inherit;
	text-decoration: none;
	transition: color 0.2s; }

.container {
	max-width: 1234px;
	margin: 0 auto;
	padding: 0 15px;

	@include sm {
		padding: 0 10px; } }
