.benefits-sect {
	padding: 20px 0 29px 0;
	background-color: #353744;

	@include sm {
		padding: 18px 0 18px; }

	&__list {
		display: flex;
		align-items: flex-end;

		@include xs {
			flex-direction: column; } }

	&__item {
		position: relative;
		width: 33.33%;

		&:not(:last-child)::after {
			content: "";
			position: absolute;
			top: 50%;
			transform: translate(0, -50%);
			right: -5px;
			width: 10px;
			height: 250px;
			background-image: url(../img/icon/line-decor.png);
			background-repeat: no-repeat;
			background-size: contain;

			@include xs {
				right: auto;
				left: 50%;
				transform: translate(-50%, 0) rotate(-90deg); } }

		@include sm {
			padding: 20px; }

		@include xs {
			width: 100%; } } }
