.burger {
	display: none;
	position: relative;
	background: 0 0;
	border: 0;
	user-select: none;
	cursor: pointer;

	@include sm {
		display: flex; }

	& > span {
		position: relative;
		display: inline-block;
		width: 30px;
		height: 3px;
		vertical-align: middle;
		background: #63b250;
		transition: background .3s .5s;

		&::after,
		&::before {
			content: "";
			position: absolute;
			left: 0;
			width: 100%;
			height: 3px;
			background: #63b250;
			-webkit-transform-origin: 50% 50%;
			transform-origin: 50% 50%;
			transition: top .3s .3s, transform .3s; }

		&::after {
			top: -10px; }

		&::before {
			top: 10px; } } }
