.dots-diamond {
	li {
		display: inline-block;
		width: 30px;

		&.slick-active {
			button {
				opacity: 1; } }

		&:hover:not(.slick-active) {
			button {
				opacity: .85; } } }

	button {
		color: black;
		text-indent: -9999px;
		display: block;
		border: 2px solid;
		height: 12px;
		width: 12px;
		line-height: 0;
		margin: 0 3px;
		background-color: #f4f7f9;
		transform: rotate(-45deg);
		transition: all 0.3s ease;
		border: 4px solid hsla(0, 0%, 0%, 0.4);
		box-sizing: content-box;
		background-clip: padding-box;
		border-radius: 5px;
		opacity: 0.4; } }
