.btn-slick {
	$this: &;
	z-index: 1;
	display: flex;
	position: absolute;
	top: 50%;
	transform: translate(0, -50%);
	width: 50px;
	height: 50px;
	background-color: rgba(0, 0, 0, 0.4);
	border-radius: 50%;
	cursor: pointer;

	&:hover {
		opacity: 0.8;

		&:after {
			opacity: 1; } }

	&--prev {
		left: 19px; }

	&--next {
		right: 25px;

		@include sm {
			right: 19px; } }

	&--prev:after,
	&--next:after {
		margin: auto;
		z-index: 5;
		content: "";
		width: 0;
		height: 0;
		border-top: 10px solid transparent;
		border-bottom: 10px solid transparent;
		opacity: 0.4; }

	&--prev:after {
		border-right: 15px solid white; }

	&--next:after {
		border-left: 15px solid white; } }
