.benefit {
	$this: &;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	align-items: center;

	&:hover {
		opacity: 0.75;

		#{$this}__title {
			transform: translate(0, 10px); } }

	&__media {
		margin-bottom: 23px; }

	&__img {
		max-width: 100%;
		height: auto;

		&--cultivation {
			position: relative;
			top: 8px;
			left: 2px; } }

	&__title {
		display: block;
		margin-bottom: 22px;
		min-height: 35px;
		text-align: center;
		color: #fff;
		font-size: 14px;
		font-weight: 700;
		text-transform: uppercase;
		text-decoration: none;
		transition: transform .3s ease-in-out; } }
