.accordeon {
	$this: &;

	&__item {
		display:  inline-block;
		width: 100%;
		border-bottom: 2px solid #dadadc;

		&.is-active {

			#{$this}__title {
				color: #63b250;

				&::after {
					transform: rotate(90deg); } } } }

	&__title {
		display: block;
		position: relative;
		padding: 18px 38px;
		font-size: 30px;
		line-height: 1;
		font-weight: 700;
		color: #353744;
		cursor: pointer;
		user-select: none;
		touch-action: manipulation;
		transition: color .25s ease;

		&::after {
			content: "";
			position: absolute;
			display: inline-block;
			width: 0;
			height: 0;
			top: 50%;
			transform: translate(0, -50%);
			left: 15px;
			border: 9px solid transparent;
			border-left: 12px solid #63b250;
			transition: transform .25s ease;
			transform-origin: 52% 34%;

			@include xs {
				left: 5px; } }

		&:hover {
			color: #63b250; }

		@include xs {
			padding: 18px 31px;
			font-size: 18px;
			line-height: 1.2; } }

	&__content {
		display: none;
		padding: 13px 130px 32px 39px;

		@include sm {
			padding: 8px 50px 22px 39px; }

		@include xs {
			padding: 5px 10px 12px 32px; }

		img {
			max-width: 100%;
			height: auto;
			margin-bottom: 14px;
			border: 1px solid #d6d7d8; }

		p {
			margin-bottom: 15px;
			font-size: 20px;
			line-height: 1.2;
			font-weight: 400;
			color: #353744;

			@include xs {
				font-size: 16px; } } } }
