}

.benefit {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

.benefit:hover {
  opacity: 0.75;
}

.benefit:hover .benefit__title {
  transform: translate(0, 10px);
}

.benefit__media {
  margin-bottom: 23px;
}

.benefit__img {
  max-width: 100%;
  height: auto;
}

.benefit__img--cultivation {
  position: relative;
  top: 8px;
  left: 2px;
}

.benefit__title {
  display: block;
  margin-bottom: 22px;
  min-height: 35px;
  text-align: center;
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  transition: transform .3s ease-in-out;
}

.footer {
  padding-top: 41px;
  padding-bottom: 34px;
}

.footer__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media only screen and (max-width: 599px) {
  .footer__wrap {
    flex-direction: column;
    justify-content: center;
  }
}

.footer__copiright {
  color: #313238;
  font-size: 20px;
  font-weight: 400;
}

@media only screen and (max-width: 599px) {
  .footer__copiright {
    margin-bottom: 20px;
    font-size: 18px;
  }
}

.footer__logo {
  display: block;
}

.footer__logo-img {
  width: 150px;
  height: 30px;
  object-fit: cover;
}

.burger {
  display: none;
  position: relative;
  background: 0 0;
  border: 0;
  user-select: none;
  cursor: pointer;
}

@media only screen and (max-width: 767px) {
  .burger {
    display: flex;
  }
}

.burger > span {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 3px;
  vertical-align: middle;
  background: #63b250;
  transition: background .3s .5s;
}

.burger > span::after, .burger > span::before {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 3px;
  background: #63b250;
  -webkit-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
  transition: top .3s .3s, transform .3s;
}

.burger > span::after {
  top: -10px;
}

.burger > span::before {
  top: 10px;
}

.tabs-sect {
  padding: 50px 0 118px 0;
  background-color: #f7f7f7;
}

@media only screen and (max-width: 599px) {
  .tabs-sect {
    padding: 50px 0;
  }
}
