.nav {
	$this: &;
	display: block;

	&.is-open {
		#{$this}__menu {
			display: block; }

		.burger__line {
			background: 0 0;

			&::before,
			&::after {
				top: 0;
				transition: top .3s,transform .3s .5s; }

			&::before {
				transform: rotate3d(0,0,1,45deg); }

			&::after {
				transform: rotate3d(0,0,1,-45deg); } } }

	@include sm {
		justify-content: space-between; }

	&__menu {
		display: flex;
		justify-content: center;
		align-items: center;

		@include sm {
			display: none;
			position: fixed;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			z-index: 20;
			min-width: 320px;
			padding-top: 15px;
			padding-right: 16px;
			padding-left: 65px;
			overflow-y: auto;
			color: #fff;
			background-color: #353744; } }

	&__burger {
		top: 0;
		right: 0;
		z-index: 25;
		justify-content: center;
		align-items: center;
		box-sizing: content-box;
		width: 30px;
		height: 30px;
		padding-left: 15px; }

	&__list {
		display: flex;
		align-items: center;
		width: 50%;
		padding: 0 100px;

		@include sm {
			flex-direction: column;
			align-items: flex-start;
			width: 100%;
			padding: 0; }

		&--left {
			justify-content: flex-end;

			@include sm {
				justify-content: flex-start; } }

		&--right {
			justify-content: flex-start; } }

	&__item {
		padding: 5px 28px 5px 12px;

		@include sm {
			padding: 0; }

		&:last-child {
				padding-right: 0;

				@include sm {
					padding-right: 10px; } } }

	&__link {
		position: relative;
		display: block;
		color: #353744;
		font-size: 14px;
		font-weight: 700;
		text-transform: uppercase;
		text-decoration: none;

		&:hover,
		&:focus {
			color: #63b250;

			&::before {
				top: 109%;
				opacity: .6; } }

		&:active {
			opacity: 0.9; }

		&::before {
			content: "";
			position: absolute;
			top: 80%;
			left: 0;
			right: 0;
			border-bottom: 2px solid #63b250;
			opacity: 0;
			transition: opacity .25s,top .25s;

			@include sm {
				display: none; } }


		@include sm {
			padding: 10px 15px;
			color: #fff; } } }
