.header {
	position: relative;
	z-index: 2;
	height: 104px;
	background-color: #fff;
	display: flex;
	align-items: center;
	filter: drop-shadow(0px 10px 0px rgba(53, 55, 68, 0.1));

	&::before {
		content: '';
		position: absolute;
		left: 0;
		right: 0;
		height: 150px;
		width: 137px;
		margin: auto;
		border-radius: 50%;
		background-color: inherit;
		height: 177px;
		width: 258px; }

	@include sm {
		filter: none; }

	&__wrap {
		position: relative;
		width: 100%; }

	&__logo {
		position: absolute;
		align-self: flex-start;
		top: -14px;
		left: 50%;
		transform: translate(-50%, 0); }

	&__language {
		position: absolute;
		top: 50%;
		right: 22px;
		transform: translate(0,-50%); } }
